nav {
  display: flex;
  position: fixed;
  background-color: rgba(91, 6, 112, 0.3);
  padding: 0.5rem 1.5rem;
  z-index: 2;
  left: 80%;
  transform: translateX(-50%);
  bottom: 2rem;
  gap: 0.5rem;
  border-radius: 2rem;
  backdrop-filter: blur(15px);
  display: none;
}

nav a {
  background: transparent;
  padding: 1rem;
  border-radius: 50%;
  display: flex;
  color: white;
  font-size: 1.1rem;
}
nav a:hover {
  background-color: rgba(107, 107, 107, 0.3);
}
nav a.active {
  background-color: rgba(107, 107, 107, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  text-decoration: none;
  color: black;
}
nav a.null {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  text-decoration: none;
  color: black;
}
@media all and (min-width: 300px) and (max-width: 767px) {
  nav {
    display: flex;
    position: fixed;
    background-color: rgba(107, 107, 107, 0.3);
    padding: 0.5rem 0.5rem;
    z-index: 20;
    left: 50%;
    transform: translateX(-50%);
    bottom: 1rem;
    gap: 0px;
    border-radius: 2rem;
    backdrop-filter: blur(15px);
  }
  nav a {
    background: transparent;
    padding: 0.5rem;
    border-radius: 50%;
    display: flex;
    font-size: 1.1rem;
  }
  nav a:hover {
    background-color: rgba(107, 107, 107, 0.3);
  }
  nav a.active {
    background-color: rgba(107, 107, 107, 0.3);
  }
}
