#gitname {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  margin: auto;
  border-radius: 15px;
  font-size: 33px;

  margin-bottom: 30px;
}

#procard {
  width: 85%;
  margin: auto;
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(1, 1fr);
}

#gitstats {
  gap: 60px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: auto;
  width: 85%;
}
#gitstats > img {
  width: 100%;
  border-radius: 1rem;
  border: 5px solid var(--color-bg);
  /* box-shadow: var(--color-bg) 0px 10px 20px, var(--color-bg) 0px 6px 6px; */
}
#calendar {
  width: 70%;
  margin: auto;
  text-align: center;
  justify-content: center;
  align-items: center;
}
@media all and (min-width: 300px) and (max-width: 1030px) {
  #procard {
    grid-template-columns: repeat(1, 1fr);
  }
  #gitstats {
    gap: 30px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    margin: auto;
    width: 85%;
  }
  #gitname {
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    margin: auto;
    margin-top: 30px;
    border-radius: 15px;
    font-size: 25px;

    margin-bottom: 30px;
  }
}
