#contactform {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 85%;
  margin: auto;

  border-radius: 1rem;
  color: white;
  /* background-color: var(--color-bg) */
  background-color: var(--color-bg);
}
#tech {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  margin: auto;
  border-radius: 15px;
  font-size: 35px;
  margin-top: -30px;
  margin-bottom: 30px;
}
/* #contactform div{
    padding:10px
} */
#contactform a {
  color: var(--color-bg);
}
#contactform > :nth-child(2) {
  background-color: white;
  color: var(--color-heading);
  width: 75%;
}
#contactform > :nth-child(1) > div {
  border: 1px solid var(--color-bg);
}
#contactform > div {
  width: 60%;
  align-items: center;
  height: 90%;
  margin: auto;
  border-radius: 1rem;
  padding: 20px;
}
#contactform input {
  font-size: 19px;
  font-weight: bold;
  padding: 7px;
  width: 80%;
  border: 2px solid var(--color-bg);

  border-radius: 5px;
  /* border: 2px solid var(--color-bg) */
}
#formbu button {
  margin-top: 16px;
  background-color: var(--color-bg);
  color: white;
  width: 50%;
}
#formsub {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  width: 70%;
  margin-top: 30px;
  border-radius: 3rem;
  border: 8px solid var(--color-bg);
  box-shadow: var(--color-bg) 0px 10px 20px, var(--color-bg) 0px 6px 6px;
  margin: auto;
  margin-top: 30px;
}
#contactlinkdiv {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 20px;
}
#contactlinkdiv div {
  width: 90%;
  text-align: center;
  padding: 5px;
  color: var(--color-heading);
  background-color: white;
  border-radius: 2rem;
}
#contactlinkdiv a {
  text-decoration: none;
}

#socialgit {
  display: flex;
  justify-content: space-around;
}
#socialgit a {
  font-size: 35px;
  color: var(--color-bg);
}
@media all and (min-width: 300px) and (max-width: 767px) {
  #contactform {
    width: 95%;
    grid-template-columns: repeat(1, 1fr);
  }
  #sociallinks {
    margin: auto;
    margin-top: 20px;
    display: flex;
    color: rgb(255, 255, 255);
    width: 40%;
  }
  #sociallinks a {
    font-size: 40px;
    color: rgb(255, 255, 255);
  }
  #contactform input {
    font-size: 17px;
    font-weight: bold;
    padding: 7px;
    width: 80%;
    border: 2px solid var(--color-bg);

    border-radius: 5px;
    /* border: 2px solid var(--color-bg) */
  }
  #formsub {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    width: 85%;
    margin-top: 30px;
    border-radius: 3rem;
    border: 8px solid var(--color-bg);
    box-shadow: var(--color-bg) 0px 10px 20px, var(--color-bg) 0px 6px 6px;
    margin: auto;
    font-size: 14px;
    margin-top: 30px;
  }

  #formbu button {
    margin-bottom: 10px;
  }
}
